.Main{
  height: 100%;
  position: relative;
       .container{
           
            width: 80%;
            height: 80%;
            color: rgb(255, 255, 255);
            background-color: rgb(32, 47, 48,0.2);
            box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
            border-radius: 20px;
            padding: 20px;
            padding-top:10px;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            display: flex;
            flex-flow: row wrap;
            &::-webkit-scrollbar{
                width: 10px;
                //background-color: rgb(0, 11, 71);
            }
            &::-webkit-scrollbar-thumb {
                direction:rtl;
                border-radius: 5px;
                background: rgb(230, 242, 255);
              }
        
            > *{
                border-radius: 5px;
                padding-top: 70px;
                
                scroll-snap-align: start;
            }
            flex:2;
            .container1{
                flex: 1 ;
                padding-top:20px ;
                width: 500px;
                min-width: 300px;
                width: 900px;
                font-size: 20px;
                margin-bottom: 20px;
               
            }
            .container2{
                position: relative;
                width: 500px;
                height: 900px;
                flex: 1 ;
                min-width: 500px;
                
            }
            
            
            
            
       }
       
}
